      height: inherit;
      .sub_filter_section {
        flex-wrap: wrap;
        .ft_tags {
          margin-top: 10px;
          margin-left: 0px;
          width: 100%;
          .tag_item {
            padding: 0px;
            margin-right: 15px;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 1350px) and (min-width: 900px) {
  .header {
    .search_section {
      input {
        width: 240px;
      }
    }
    .main_menu {
      & > ul > li {
        margin-left: 20px;
      }
    }
  }
  .contents_wrap {
    .filter_section {
      .ft_process {
        .ftp_item {
          .ftp_num {
            border: 1px solid #ddd;
            border-radius: 50%;
            width: 24px;
            height: 24px;
            display: inline-block;
            text-align: center;
            line-height: 24px;
          }
          img {
            opacity: 0.5;
          }
          .ftp_t {
            display: none;
          }
        }
      }
    }
    .intro {
      &.intro3 {
        .deco_img {
          img {
            width: 350px;
          }
        }
      }
      &.intro5 {
        padding: 160px 30px;
        .price_items {
          .pr_item {
            .pr_tags {
              flex-direction: column;
              .pr_tag {
                margin-bottom: 10px;
                text-align: center;
              }
            }
            .pr_contents {
              .pr_desc {
                margin-top: 20px;
              }
            }
          }
        }
      }
    }
    .tutorial_items {
      .tt_item {
        width: calc(33.3% - 30px);
        .tt_contents {
          padding: 12px 15px;
          .tt_bottom {
            padding: 0px 15px;
            bottom: 10px;
          }
        }
      }
    }
    .price_section {
      .price_items {
        .price_item {
          padding: 20px 30px;
        }
      }
    }
    .price_detail_section {
      .pr_container {
        min-width: 1100px;
      }
    }
    .default_bt {
      &.price_sub_bt {
        padding: 0px 15px;
      }
    }
  }
}

.tab_creatorMarket {
  position: relative;
}
.tab_creatorMarket:hover {
  .creatorMarket_dropdown {
    font-family: 'Roboto', 'Noto Sans KR', 'Noto Sans', sans-serif;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 17px;
    left: -15%;
    width: 170px;
    padding: 15px 0px;
    text-align: left;
    filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.1));
    background-color: #fff;
    font-weight: 700;
    border-radius: 15px;
    z-index: 1000;
    border: 1px solid #eee;
    color: $black;

    a {
      width: 122px;
      line-height: 36px;
      padding: 0px 25px;
      cursor: pointer;
      position: relative;
      font-size: 15px;

      &:hover {
        background-color: #eee;
        color: $black;
      }
    }
  }
}
.creatorMarket_dropdown {
  font-weight: 500;
  display: none;
}
